<template>
  <div>
    <v-row justify="center"></v-row>
    <v-row id="serchEmployee">
      <v-col cols="12" md="3">
        <v-autocomplete
          v-model="empId"
          :items="tzArray"
          color="indigo lighten-5"
          :item-text="item => item.firstName +' '+ item.lastName + ' - ' + item.empId"
          hide-selected
          item-value="empId"
          label="תעודת זהות"
          placeholder="חפש"
          @change="getEmployeeInfo()"
        ></v-autocomplete>
      </v-col>
      <v-btn color="info" @click="createNewEmp">צור עובד חדש</v-btn>
    </v-row>

    <v-card v-if="createNew == true || empInfo.empId != undefined" class="wrapper">
      <form @submit.prevent id="myForm">
        <v-row>
          <v-col cols="12" md="2">
            <v-text-field v-model="empInfo.empId" label="תעודת זהות" required></v-text-field>
          </v-col>
        </v-row>
        <div id="empDetails">
          <v-row>
            <v-col cols="12" md="2">
              <v-text-field v-model="empInfo.firstName" :rules="nameRules" label="שם פרטי" required></v-text-field>
            </v-col>
            <v-col cols="12" md="2">
              <v-text-field v-model="empInfo.lastName" :rules="nameRules" label="שם משפחה" required></v-text-field>
            </v-col>
          </v-row>
          <v-row>
            <v-col cols="12" md="2">
              <p>מין</p>
              <v-radio-group v-model="empInfo.gender" :mandatory="false">
                <v-radio label="זכר" value="M"></v-radio>
                <v-radio label="נקבה" value="F"></v-radio>
              </v-radio-group>
            </v-col>
            <v-col cols="12" md="2">
              <p>משרת אם</p>
              <v-radio-group v-model="empInfo.mother" :mandatory="false">
                <v-radio label="כן" value="true"></v-radio>
                <v-radio label="לא" value="false"></v-radio>
              </v-radio-group>
            </v-col>
            <v-col cols="12" md="3">
              <label for="birthDate">תאריך לידה</label>
              <input type="date" name="birthDate" v-model="empInfo.birthDate" />
            </v-col>
          </v-row>
          <v-row>
            <v-btn
              color="success"
              type="submit"
              :disabled="!validForm"
              class="mr-4"
              @click="validate"
            >אישור טופס</v-btn>
            <v-btn class="myBtn" color="red" @click="deleteEmployer()">מחק עובד</v-btn>
          </v-row>
        </div>
        <div class="text-center">
          <v-dialog v-model="dialog" width="500">
            <v-card>
              <v-card-text>העובד נשמר בהצלחה</v-card-text>
              <v-divider></v-divider>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="primary" text @click="dialog = false">OK</v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </div>
      </form>
    </v-card>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "HireEmployee",
  data() {
    return {
      tzArray: [],
      dialog: false,
      validForm: true,
      createNew: false,
      idRules: [
        (v) => !!v || "שדה חובה",
        (v) => v.length == 9 || "בתעודת זהות נדרש 9 ספרות",
      ],
      nameRules: [(v) => !!v || "שדה חובה"],
      empInfo: {},
      empId: null,
    };
  },
  created() {
    this.getAllTz();
  },
  methods: {
    FormatDate(iDate) {
      var inputDate = new Date(iDate);
      var formattedDate;
      var year = inputDate.getFullYear();
      var month = 0;
      var day = 0;

      month += inputDate.getMonth() + 1;
      if (month < 10) {
        month = "0" + month;
      }
      if (inputDate.getDate() < 10) {
        day = "0";
      }
      day += inputDate.getDate();
      formattedDate = year + "-" + month + "-" + day;
      return formattedDate;
    },
    validate() {
      if (this.saveEmployer()) {
        this.dialog = true;
      }
    },
    getEmployeeInfo() {
      this.empInfo = this.tzArray.find((el) => el.empId == this.empId);
      this.empInfo.mother = "" + this.empInfo.mother + "";
      this.empInfo.birthDate = this.FormatDate(
        new Date(this.empInfo.birthDate)
      );
    },
    saveEmployer() {
      if (
        this.empInfo.empId == null ||
        this.empInfo.firstName == null ||
        this.empInfo.lastName == null ||
        this.empInfo.birthDate == null ||
        this.empInfo.gender == null ||
        this.empInfo.mother == null
      ) {
        alert("נא להזין את כל השדות");
        return;
      }
      axios
        .post(`/employees/save`, {
          empId: this.empInfo.empId,
          firstName: this.empInfo.firstName,
          lastName: this.empInfo.lastName,
          birthDate: this.empInfo.birthDate,
          gender: this.empInfo.gender,
          mother: this.empInfo.mother,
        })
        .then(() => {
          alert("פרטי העובד נשמרו בהצלחה");
          document.getElementById("myForm").submit();
        })
        .catch((error) =>
          this.$store.dispatch("displayErrorMessage", {
            error,
          })
        );
    },
    getAllTz() {
      axios
        .get("/employees/all")
        .then((response) => {
          this.tzArray = response.data;
        })
        .catch((error) =>
          this.$store.dispatch("displayErrorMessage", {
            error,
          })
        );
    },
    deleteEmployer() {
      axios
        .delete("/employees/byId", {
          params: {
            empId: this.empInfo.empId,
          },
        })
        .then(() => {
          alert("העובד נמחק בהצלחה");
          document.getElementById("myForm").submit();
        })
        .catch((error) =>
          this.$store.dispatch("displayErrorMessage", {
            error,
          })
        );
    },
    createNewEmp() {
      if (document.getElementById("myForm") != null) {
        document.getElementById("myForm").reset();
      }
      this.empInfo = {};
      this.createNew = true;
    },
  },
};
</script>

<style scoped>
.wrapper {
  margin: 3%;
  padding: 3%;
}
input {
  border: 1px solid;
}
.header {
  justify-content: center;
  font-weight: bold;
  text-decoration: underline;
}
</style>
